.container {
    width: 100%;
    margin: 0 auto;
    padding-left: 20px;
    padding-right: 20px;
  
    @media screen and (min-width: 480px) {
      width: 480px;
    }
  
    @media screen and (min-width: 768px) {
      width: 768px;
      padding-left: 34px;
      padding-right: 34px;
    }
  
    @media screen and (min-width: 1280px) {
      width: 1280px;
      padding-left: 115px;
      padding-right: 115px;
    }
  }
  